import { createWebHistory, createRouter } from 'vue-router'
import Home from '../components/Home'
import Left from '../components/Left'


import Menu_old from '../components/Menu_old'
import Menu from '../components/Menu'

import Menu1 from '../components/Menu1/Menu1'
import Menu1_Nav from '../components/Menu1/Menu1_Nav'
import Menu1_1 from '../components/Menu1/Menu1_1'
import Menu1_2 from '../components/Menu1/Menu1_2'
import Menu1_3 from '../components/Menu1/Menu1_3'

import Menu2 from '../components/Menu2/Menu2'
import Menu2_Nav from '../components/Menu2/Menu2_Nav'
import Menu2_Contents from '../components/Menu2/Menu2_Contents'
import Menu2_1 from '../components/Menu2/Menu2_1'
import Menu2_2 from '../components/Menu2/Menu2_2'
import Menu2_3 from '../components/Menu2/Menu2_3'

import Menu3 from '../components/Menu3/Menu3'
import Menu3_Nav from '../components/Menu3/Menu3_Nav'
import Menu3_1 from '../components/Menu3/Menu3_1'
import Menu3_2 from '../components/Menu3/Menu3_2'
import Menu3_3 from '../components/Menu3/Menu3_3'

import Menu4 from '../components/Menu4/Menu4'
import Menu4_Nav from '../components/Menu4/Menu4_Nav'
import Menu4_1 from '../components/Menu4/Menu4_1'
import Menu4_2 from '../components/Menu4/Menu4_2'
import Menu4_3 from '../components/Menu4/Menu4_3'

// import Nav from '../components/Nav'
import Page1 from '../components/Menu1/Page1'
import Page2 from '../components/Menu1/Page2'
import DigitalClock from '../components/DigitalClock.vue'

// const Home = { template: "<div>Home</div>" }
// const NotFound = { template: "<div>Not Found</div>" }

const routes = [
    {
        path: '/',          // 경로
        name: 'home',           // 해당 경로의 이름(참고)
        component: Home,     // 이동할 컴포넌트(router-view 영역에 보여줄 컴포넌트를 지정)
    },
    
    { path: '/left', name: 'left', component: Left },
    { path: '/menu_old', name: 'menu_old', component: Menu_old },
    { path: '/menu', name: 'menu', component: Menu },
    {path: '/clock', name: 'clock', component: DigitalClock},

    { path: '/menu1', name: 'menu1', component: Menu1 },
    { path: '/menu1_nav', name: 'menu1_nav', component: Menu1_Nav },
    { path: '/menu1_1', name: 'menu1_1', component: Menu1_1 },
    { path: '/menu1_2', name: 'menu1_2', component: Menu1_2 },
        // { path: '/nav', name: 'nav', component: Nav},
        {path: '/page1', name: 'page1', component: Page1},
        {path: '/page2', name: 'page2', component: Page2},
    { path: '/menu1_3', name: 'menu1_3', component: Menu1_3 },

    { path: '/menu2', name: 'menu2', component: Menu2 },
    { path: '/menu2_nav', name: 'menu2_nav', component: Menu2_Nav },
    { path: '/menu2_1', name: 'menu2_1', component: Menu2_1 },
    { path: '/menu2_2', name: 'menu2_2', component: Menu2_2 },
    { path: '/menu2_3', name: 'menu2_3', component: Menu2_3 },
    { path: '/menu2_contents', name: 'menu2_contents', component: Menu2_Contents },

    { path: '/menu3', name: 'menu3', component: Menu3 },
    { path: '/menu3_nav', name: 'menu3_nav', component: Menu3_Nav },
    { path: '/menu3_1', name: 'menu3_1', component: Menu3_1 },
    { path: '/menu3_2', name: 'menu3_2', component: Menu3_2 },
    { path: '/menu3_3', name: 'menu3_3', component: Menu3_3 },
    // { path: '/menu3_contents', name: 'menu3_contents', component: Menu3_Contents },

    { path: '/menu4', name: 'menu4', component: Menu4 },
    { path: '/menu4_nav', name: 'menu4_nav', component: Menu4_Nav },
    { path: '/menu4_1', name: 'menu4_1', component: Menu4_1 },
    { path: '/menu4_2', name: 'menu4_2', component: Menu4_2 },
    { path: '/menu4_3', name: 'menu4_3', component: Menu4_3 },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;
